.sweet__list li{display:flex;align-items:flex-start;padding:20px;border-top:1px solid #eff3f4}.sweet__list li .sweet_icon{margin-right:20px}.sweet__list li .sweet_icon img{width:50px;height:50px;border-radius:50px}.sweet__list li .sweet_content{position:relative;flex:1 1 auto}.sweet__list li .sweet_content .sweet_info{font-weight:bold}.sweet__list li .sweet_content .sweet_info span{color:#536471;font-weight:normal}.sweet__list li .sweet_content .sweet_menu{position:absolute;top:-10px;right:0}.sweet__list li .sweet_content .sweet_menu button{margin-left:5px;font-size:11px;padding:5px 10px;background-color:#fff;color:#1d9bf0;border-radius:5px}.sweet__list li .sweet_content .sweet_menu button:hover{background-color:#1d9bf0;color:#fff}.sweet__list li .sweet_content .sweet_text{margin:20px 0}.sweet__list li .sweet_content .sweet_text p{margin-bottom:20px;line-height:1.5}.sweet__list li .sweet_content .sweet_text .sweet_image{position:relative}.sweet__list li .sweet_content .sweet_text .sweet_image img{width:100%;max-height:500px;object-fit:cover;border-radius:20px}.sweet__list li .sweet_content .sweet_text .sweet_image button{position:absolute;top:10px;right:10px}.sweet__list li .sweet_content .sweet_text form{width:100%;text-align:right}.sweet__list li .sweet_content .sweet_text form textarea{width:100%;padding:10px;font-size:16px;font-family:"맑은 고딕",sans-serif}.sweet__list li .sweet_content .sweet_text form button{margin:15px 0;padding:7px 15px;background-color:#1d9bf0;font-size:14px;font-weight:bold;color:#fff;border-radius:10px}.sweet__list li:hover{background-color:#f7f7f7}.isLoading{padding:20px;background-color:#1d9bf0;color:#fff}